          label: '85 - 4.038'
        },
        {
          value: 3.325,
          label: '7 - 3.325'
        },],
      [{
        value: 3.25,
        label: ' - 3.25'
      },
        {
          value: 4.9,
          label: ' - 4.9'
        },
        {
          value: 5.39,
          label: '1.1 - 5.39'
        },
        {
          value: 4.165,
